<template>
    <div class="profile">
        <v-container>
            <cloud style="max-width: 400px">
                <v-hover>
                    <template v-slot:default="{ hover }">
                        <div style="position: relative; text-align: center">
                            <v-fade-transition>
                                <v-icon v-if="$store.state.user.information.image === ''" color="gold" style="font-size: 80px;" class="py-4">mdi-account-outline</v-icon>
                                <v-img v-else :src="$store.state.user.information.image" class="profile-image"></v-img>
                            </v-fade-transition>
                            <v-fade-transition>
                                <v-overlay
                                        v-if="hover"
                                        absolute
                                        color="wenge"
                                        light
                                >
                                    <uploader :minAspectRatio="1.2" :commit="'user/setImage'"></uploader>
                                </v-overlay>
                            </v-fade-transition>
                        </div>
                    </template>
                </v-hover>
                <v-row class="mx-0">
                    <v-col class="pa-0 ma-0" cols="10">
                         <v-card-title>{{$store.state.user.information.name}}</v-card-title>
                    </v-col>
                    <v-col class="pa-0 ma-0 d-flex align-center justify-center" cols="2">
                        <v-btn fab color="gold" x-small>
                            <v-icon color="wenge">mdi-pencil-outline</v-icon>
                        </v-btn>
                    </v-col>
                </v-row>
                <v-row class="mx-0">
                    <v-col class="pa-0 ma-0" cols="10">
                        <v-card-text>{{$store.state.user.information.email}}</v-card-text>
                    </v-col>
                    <v-col class="pa-0 ma-0 d-flex align-center justify-center" cols="2">
                        <v-btn fab color="gold" width="24" height="24">
                            <v-icon small color="wenge">mdi-pencil-outline</v-icon>
                        </v-btn>
                    </v-col>

                </v-row>
            </cloud>
        </v-container>
    </div>
</template>

<script>
    import Cloud from "../components/ui-components/sky/Cloud";
    import Uploader from "../components/ui-components/Uploader";
    export default {
        name: "Profile",
        components: { Uploader, Cloud},
    }
</script>

<style lang="scss">
    .profile {
        .v-overlay__content {
            width: 100%;
            height: 100%;
        }
        .profile-image-button {
            position: absolute;
            bottom: 15px;
            right: 20px;
        }

        #profile-foto-camera,
        #profile-foto-upload{
            opacity: 0;
            width: 40px;
            height: 40px;
        }

        .profile-foto {
            position: relative;
            cursor: pointer;
            &:first-child {
                margin-right: 15px;
            }
            label {
                position: absolute;
                top: 0;
                left: 0;
                cursor: pointer !important;
            }
        }

        .profile-image {
            border-bottom: 1px solid var(--wood);
        }
    }
</style>